const initMobileAccordion = () => {
  const breakpoint = window.matchMedia('(max-width:1023px)');
  const mobileParent = document.querySelector('[data-mobile-accordion="parent"]');
  const mobileElements = document.querySelectorAll('[data-mobile-accordion="element"]');
  const mobileButtons = document.querySelectorAll('[data-mobile-accordion="button"]');
  const mobileContents = document.querySelectorAll('[data-mobile-accordion="content"]');

  if (mobileParent) {
    const breakpointChecker = () => {
      if (breakpoint.matches) {
        mobileParent.setAttribute('data-accordion', 'parent');
        mobileElements.forEach((element) => {
          element.setAttribute('data-accordion', 'element');
        });
        mobileButtons.forEach((button) => {
          button.setAttribute('data-accordion', 'button');
        });
        mobileContents.forEach((content) => {
          content.setAttribute('data-accordion', 'content');
        });
      } else {
        mobileParent.removeAttribute('data-accordion', 'parent');
        mobileElements.forEach((element) => {
          element.removeAttribute('data-accordion', 'element');
        });
        mobileButtons.forEach((button) => {
          button.removeAttribute('data-accordion', 'button');
        });
        mobileContents.forEach((content) => {
          content.removeAttribute('data-accordion', 'content');
        });
      }
    };

    breakpoint.addListener(breakpointChecker);
    breakpointChecker();
  }
};

export {initMobileAccordion};
